import { Component, OnInit, AfterViewInit, ViewChild } from '@angular/core';
import { DocumentPackageService } from '@entity';
import { User } from '@model';
import { SessionService } from '@session';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { AncillaryModalComponent } from '@app/ancillary/ancillary-modal.component';

@Component({
  selector: 'sp-enrolled',
  templateUrl: './enrolled.component.html'
})
export class EnrolledComponent implements OnInit, AfterViewInit {
  user$: Observable<User>;
  signedEnvelopeId$: Observable<string>;
  @ViewChild(AncillaryModalComponent)
  ancillaryModal: AncillaryModalComponent;

  payrollLoginUrl = environment.payrollLoginUrl;

  constructor(sessionService: SessionService,
    private documentPackageService: DocumentPackageService,
  ) {
    this.user$ = sessionService.currentUser$;
  }

  ngOnInit() {
    // id of the first completed, non-removed envelope with an embedded envelope signer (if exists).
    this.signedEnvelopeId$ = this.documentPackageService.loadSignedEnvelopeId();
  }
  ngAfterViewInit() {
    setTimeout(() => this.ancillaryModal.checkAncillaryResponse());
  }
}
