import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { SharedModule } from '@shared/shared.module';
import { AncillaryModule } from '@app/ancillary/ancillary.module';

import { EnrolledComponent } from './enrolled.component';

const routes: Routes = [
  {
    path: '',
    pathMatch: 'full',
    component: EnrolledComponent
  }];

@NgModule({
  imports: [SharedModule, AncillaryModule, RouterModule.forChild(routes)],
  exports: [EnrolledComponent],
  declarations: [EnrolledComponent]
})
export class EnrolledModule {}
